      let w = this.whichsearchs
      let s = this.searchLogos
      if (this.whichsearch == w[0] && this.searchLogo == s[0]) {
        this.whichsearch = w[1]
        this.searchLogo = s[1]
      } else {
        this.whichsearch = w[0]
        this.searchLogo = s[0]
      }
